import React, { useState, useEffect, useCallback } from 'react';
import { useAuth } from '../../hooks';
import { TeamLogo, getNewsFeaturedImageUrl, getImageUrl } from '../../utils/imageUtils';
import { formatTimeAgo } from '../../utils/dateUtils';
import MobileHomePage from './MobileHomePage';

function HomePage({ navigateTo }) {
  const { api } = useAuth();
  
  // Left Column - Forums Posts
  const [forumPosts, setForumPosts] = useState([]);
  const [forumPage, setForumPage] = useState(1);
  const [forumTotal, setForumTotal] = useState(0);
  
  // Center Column - Events (live) + News (featured)
  const [liveEvents, setLiveEvents] = useState([]);
  const [eventsPage, setEventsPage] = useState(1);
  const [eventsTotal, setEventsTotal] = useState(0);
  const [featuredNews, setFeaturedNews] = useState([]);
  const [newsPage, setNewsPage] = useState(1);
  const [newsTotal, setNewsTotal] = useState(0);
  
  // Right Column - Matches
  const [matches, setMatches] = useState([]);
  const [matchesPage, setMatchesPage] = useState(1);
  const [matchesTotal, setMatchesTotal] = useState(0);
  
  const [isMobile, setIsMobile] = useState(window.innerWidth < 980);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 980);
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Fetch Forum Posts with pagination
  const fetchForumPosts = useCallback(async (page = 1) => {
    try {
      const response = await api.get(`/api/forums/threads?page=${page}&limit=10`);
      const data = response?.data?.data || response?.data || [];
      const total = response?.data?.total || data.length;
      
      if (Array.isArray(data)) {
        const formattedPosts = data.map(post => ({
          id: post.id,
          title: post.title || 'Untitled Thread',
          author: post.author?.name || post.user?.name || 'Anonymous',
          replies: post.replies_count || 0,
          views: post.views || 0,
          lastActivity: post.updated_at || post.created_at,
          category: post.category || 'General',
          isSticky: post.is_sticky || false
        }));
        
        setForumPosts(formattedPosts);
        setForumTotal(total);
      }
    } catch (error) {
      console.error('Error fetching forum posts:', error);
      setForumPosts([]);
    }
  }, [api]);

  // Fetch Live Events with pagination
  const fetchLiveEvents = useCallback(async (page = 1) => {
    try {
      const response = await api.get(`/api/events?status=live&page=${page}&limit=6`);
      const data = response?.data?.data || response?.data || [];
      const total = response?.data?.total || data.length;
      
      if (Array.isArray(data)) {
        const formattedEvents = data.map(event => ({
          id: event.id,
          name: event.name || 'Marvel Rivals Tournament',
          status: event.status || 'live',
          startDate: event.start_date,
          endDate: event.end_date,
          prizePool: event.prize_pool,
          teams: event.teams_count || 0,
          tier: event.tier || 'A',
          region: event.region || 'Global'
        }));
        
        setLiveEvents(formattedEvents);
        setEventsTotal(total);
      }
    } catch (error) {
      console.error('Error fetching live events:', error);
      setLiveEvents([]);
    }
  }, [api]);

  // Fetch Featured News with pagination
  const fetchFeaturedNews = useCallback(async (page = 1) => {
    try {
      const response = await api.get(`/api/news?featured=1&page=${page}&limit=6`);
      const data = response?.data?.data || response?.data || [];
      const total = response?.data?.total || data.length;
      
      if (Array.isArray(data)) {
        const formattedNews = data.map(news => ({
          id: news.id,
          title: news.title || 'Breaking News',
          excerpt: news.excerpt || news.content?.substring(0, 150) + '...',
          author: news.author?.name || 'MRVL Staff',
          publishedAt: news.published_at || news.created_at,
          featuredImage: news.featured_image,
          category: news.category || 'News',
          views: news.views || 0
        }));
        
        setFeaturedNews(formattedNews);
        setNewsTotal(total);
      }
    } catch (error) {
      console.error('Error fetching featured news:', error);
      setFeaturedNews([]);
    }
  }, [api]);

  // Fetch Matches with pagination
  const fetchMatches = useCallback(async (page = 1) => {
    try {
      const response = await api.get(`/api/matches?page=${page}&limit=10`);
      const data = response?.data?.data || response?.data || [];
      const total = response?.data?.total || data.length;
      
      if (Array.isArray(data)) {
        const formattedMatches = data.map(match => ({
          id: match.id,
          team1: {
            id: match.team1?.id,
            name: match.team1?.name || 'TBD',
            logo: match.team1?.logo,
            score: match.team1_score || 0
          },
          team2: {
            id: match.team2?.id,
            name: match.team2?.name || 'TBD', 
            logo: match.team2?.logo,
            score: match.team2_score || 0
          },
          event: {
            name: match.event?.name || 'Marvel Rivals Championship',
            tier: match.event?.tier || 'S'
          },
          status: match.status || 'upcoming',
          format: match.format || 'BO3',
          scheduledAt: match.scheduled_at,
          maps: match.maps || []
        }));
        
        setMatches(formattedMatches);
        setMatchesTotal(total);
      }
    } catch (error) {
      console.error('Error fetching matches:', error);
      setMatches([]);
    }
  }, [api]);

  // Load all data on component mount
  useEffect(() => {
    const loadAllData = async () => {
      setLoading(true);
      await Promise.all([
        fetchForumPosts(forumPage),
        fetchLiveEvents(eventsPage), 
        fetchFeaturedNews(newsPage),
        fetchMatches(matchesPage)
      ]);
      setLoading(false);
    };

    loadAllData();
  }, [forumPage, eventsPage, newsPage, matchesPage, fetchForumPosts, fetchLiveEvents, fetchFeaturedNews, fetchMatches]);

  // Show mobile version
  if (isMobile) {
    return <MobileHomePage navigateTo={navigateTo} />;
  }

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="flex justify-center items-center min-h-96">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-red-500 mx-auto mb-4"></div>
            <p className="text-gray-600 dark:text-gray-400">Loading Marvel Rivals content...</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-6">
      {/* 3-Column Layout */}
      <div className="grid grid-cols-12 gap-6">
        
        {/* LEFT COLUMN - Forums Posts */}
        <div className="col-span-3">
          <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700">
            <div className="px-4 py-3 border-b border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-900">
              <h2 className="text-lg font-bold text-gray-900 dark:text-white">Latest Discussions</h2>
            </div>
            
            <div className="divide-y divide-gray-200 dark:divide-gray-700">
              {forumPosts.map(post => (
                <div 
                  key={post.id} 
                  className="p-4 hover:bg-gray-50 dark:hover:bg-gray-700 cursor-pointer transition-colors"
                  onClick={() => navigateTo('thread-detail', { id: post.id })}
                >
                  <div className="flex items-start justify-between">
                    <div className="flex-1 min-w-0">
                      <h3 className="text-sm font-medium text-gray-900 dark:text-white truncate">
                        {post.title}
                      </h3>
                      <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                        by {post.author}  {formatTimeAgo(post.lastActivity)}
                      </p>
                      <div className="flex items-center gap-3 mt-2 text-xs text-gray-500 dark:text-gray-400">
                        <span>{post.replies} replies</span>
                        <span>{post.views} views</span>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
            
            {/* Forums Pagination */}
            <div className="p-4 border-t border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-900">
              <div className="flex justify-between items-center">
                <button 
                  onClick={() => setForumPage(Math.max(1, forumPage - 1))}
                  disabled={forumPage === 1}
                  className="btn btn-secondary text-xs disabled:opacity-50"
                >
                  Previous
                </button>
                <span className="text-xs text-gray-600 dark:text-gray-400">
                  Page {forumPage}
                </span>
                <button 
                  onClick={() => setForumPage(forumPage + 1)}
                  disabled={forumPosts.length < 10}
                  className="btn btn-secondary text-xs disabled:opacity-50"
                >
                  Next
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* CENTER COLUMN - Live Events + Featured News */}
        <div className="col-span-6">
          
          {/* Live Events Section */}
          <div className="mb-6 bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700">
            <div className="px-4 py-3 border-b border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-900">
              <h2 className="text-lg font-bold text-gray-900 dark:text-white">Live Events</h2>
            </div>
            
            <div className="p-4">
              {liveEvents.length > 0 ? (
                <div className="grid grid-cols-2 gap-4">
                  {liveEvents.map(event => (
                    <div 
                      key={event.id}
                      className="border border-gray-200 dark:border-gray-700 p-4 hover:border-red-500 dark:hover:border-red-400 cursor-pointer transition-colors"
                      onClick={() => navigateTo('event-detail', { id: event.id })}
                    >
                      <div className="flex items-center gap-2 mb-2">
                        <span className="w-2 h-2 bg-red-500 rounded-full animate-pulse"></span>
                        <span className="text-xs font-medium text-red-500 uppercase">Live</span>
                      </div>
                      
                      <h3 className="font-semibold text-gray-900 dark:text-white text-sm mb-2">
                        {event.name}
                      </h3>
                      
                      <div className="text-xs text-gray-600 dark:text-gray-400 space-y-1">
                        <div>Prize Pool: ${event.prizePool?.toLocaleString() || 'TBD'}</div>
                        <div>{event.teams} teams  Tier {event.tier}</div>
                        <div>{event.region}</div>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-gray-500 dark:text-gray-400 text-center py-8">
                  No live events at the moment
                </p>
              )}
            </div>
          </div>

          {/* Featured News Section */}
          <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700">
            <div className="px-4 py-3 border-b border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-900">
              <h2 className="text-lg font-bold text-gray-900 dark:text-white">Featured News</h2>
            </div>
            
            <div className="divide-y divide-gray-200 dark:divide-gray-700">
              {featuredNews.map(news => (
                <div 
                  key={news.id}
                  className="p-4 hover:bg-gray-50 dark:hover:bg-gray-700 cursor-pointer transition-colors"
                  onClick={() => navigateTo('news-detail', { id: news.id })}
                >
                  <div className="flex gap-4">
                    {news.featuredImage && (
                      <img 
                        src={getNewsFeaturedImageUrl(news.featuredImage)}
                        alt={news.title}
                        className="w-16 h-16 object-cover bg-gray-200 dark:bg-gray-700"
                      />
                    )}
                    
                    <div className="flex-1 min-w-0">
                      <h3 className="font-semibold text-gray-900 dark:text-white text-sm mb-1 line-clamp-2">
                        {news.title}
                      </h3>
                      
                      <p className="text-xs text-gray-600 dark:text-gray-400 mb-2 line-clamp-2">
                        {news.excerpt}
                      </p>
                      
                      <div className="flex items-center justify-between text-xs text-gray-500 dark:text-gray-400">
                        <span>by {news.author}</span>
                        <span>{formatTimeAgo(news.publishedAt)}</span>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
            
            {/* News Pagination */}
            <div className="p-4 border-t border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-900">
              <div className="flex justify-between items-center">
                <button 
                  onClick={() => setNewsPage(Math.max(1, newsPage - 1))}
                  disabled={newsPage === 1}
                  className="btn btn-secondary text-xs disabled:opacity-50"
                >
                  Previous
                </button>
                <span className="text-xs text-gray-600 dark:text-gray-400">
                  Page {newsPage}
                </span>
                <button 
                  onClick={() => setNewsPage(newsPage + 1)}
                  disabled={featuredNews.length < 6}
                  className="btn btn-secondary text-xs disabled:opacity-50"
                >
                  Next
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* RIGHT COLUMN - Matches */}
        <div className="col-span-3">
          <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700">
            <div className="px-4 py-3 border-b border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-900">
              <h2 className="text-lg font-bold text-gray-900 dark:text-white">Recent Matches</h2>
            </div>
            
            <div className="divide-y divide-gray-200 dark:divide-gray-700">
              {matches.map(match => (
                <div 
                  key={match.id}
                  className="p-4 hover:bg-gray-50 dark:hover:bg-gray-700 cursor-pointer transition-colors"
                  onClick={() => navigateTo('match-detail', { id: match.id })}
                >
                  <div className="space-y-2">
                    {/* Teams */}
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2 flex-1 min-w-0">
                        <TeamLogo team={match.team1} size="xs" />
                        <span className="text-sm font-medium text-gray-900 dark:text-white truncate">
                          {match.team1.name}
                        </span>
                      </div>
                      <div className="flex items-center gap-2 px-2">
                        <span className="text-sm font-bold text-gray-900 dark:text-white">
                          {match.team1.score}
                        </span>
                        <span className="text-xs text-gray-500">-</span>
                        <span className="text-sm font-bold text-gray-900 dark:text-white">
                          {match.team2.score}
                        </span>
                      </div>
                      <div className="flex items-center gap-2 flex-1 min-w-0 justify-end">
                        <span className="text-sm font-medium text-gray-900 dark:text-white truncate">
                          {match.team2.name}
                        </span>
                        <TeamLogo team={match.team2} size="xs" />
                      </div>
                    </div>
                    
                    {/* Match Info */}
                    <div className="flex items-center justify-between text-xs text-gray-500 dark:text-gray-400">
                      <span>{match.event.name}</span>
                      <span className={`px-2 py-1 text-xs font-medium ${ 
                        match.status === 'live' ? 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200' :
                        match.status === 'completed' ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200' :
                        'bg-gray-100 text-gray-800 dark:bg-gray-700 dark:text-gray-200'
                      }`}>
                        {match.status.toUpperCase()}
                      </span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
            
            {/* Matches Pagination */}
            <div className="p-4 border-t border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-900">
              <div className="flex justify-between items-center">
                <button 
                  onClick={() => setMatchesPage(Math.max(1, matchesPage - 1))}
                  disabled={matchesPage === 1}
                  className="btn btn-secondary text-xs disabled:opacity-50"
                >
                  Previous
                </button>
                <span className="text-xs text-gray-600 dark:text-gray-400">
                  Page {matchesPage}
                </span>
                <button 
                  onClick={() => setMatchesPage(matchesPage + 1)}
                  disabled={matches.length < 10}
                  className="btn btn-secondary text-xs disabled:opacity-50"
                >
                  Next
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HomePage;